	module.exports = {"data":{"wordpressPost":{"title":"The effect of statins, obesity-related behaviours in Europe and the accuracy of Wikipedia content: Most read articles in October","content":"<h1>Statins, obesity-related behaviours, and the accuracy of Wikipedia</h1>\n<p>&nbsp;</p>\n<figure id=\"attachment_897\" style=\"width: 205px\" class=\"wp-caption alignnone\"><a href=\"https://blogs.bmj.com/bmjopen/files/2015/11/596px-Lovastatin.svg_.png\"><img class=\"wp-image-897\" src=\"https://blogs.bmj.com/bmjopen/files/2015/11/596px-Lovastatin.svg_-300x257.png\" alt=\"by Panoramix303\" width=\"205\" height=\"175\" srcset=\"https://blogs.bmj.com/bmjopen/files/2015/11/596px-Lovastatin.svg_-300x257.png 300w, https://blogs.bmj.com/bmjopen/files/2015/11/596px-Lovastatin.svg_.png 596w\" sizes=\"(max-width: 205px) 100vw, 205px\" /></a><figcaption class=\"wp-caption-text\"><em>Lovastatin, from Wikipedia</em></figcaption></figure>\n<p>Several papers have entered the Top 10 Most Read this month, including Kristensen <em>et al</em>&#8216;s systematic review on statins and survival in randomised trials, which tops the list. In our tenth most read article, Azer <em>et al</em> assess the accuracy of content of Wikipedia articles on cardiovascular diseases, and at number nine, Lakerveld <em>et al</em> provide the first results of the SPOTLIGHT survey of obesity-related behaviours and body mass index in adults in Europe. Previously featured papers on the list include Deane <em>et al</em>&#8216;s identification of the top priorities for the management of Parkinson&#8217;s disease and Dall&#8217;Ora <em>et al</em>&#8216;s study on the association of long shifts for nurses and job dissatisfaction.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Kristensen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Deane <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006434.full\">Priority setting partnership to identify the top 10 research priorities for the management of Parkinson&#8217;s disease</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Hubble <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006095.full?sid=e58c8d58-d1ae-4208-afd9-d5e1ae740638\">Trunk muscle exercises as a means of improving postural stability in people with Parkinson&#8217;s disease: a protocol for a randomised controlled trial</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Dall&#8217;Ora <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e008331.full?sid=62c86224-5b21-4380-b208-49e30f594624\">Association of 12 h shifts and nurses&#8217; job satisfaction, burnout and intention to leave: findings from a cross-sectional study of 12 European countries</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Hysing <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006748.full?sid=dd37ec43-e0dd-4f1b-a420-bce97d12e7ef\">Sleep and use of electronic devices in adolescence: results from a large population-based study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Bernacki <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/10/e009032.full\">Development of the Serious Illness Care Program: a randomised controlled trial of a palliative care communication intervention</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Plouvier <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/6/e007171.full\">Transitions in Parkinson&#8217;s disease in primary care: protocol of a longitudinal mixed methods study</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Lakerveld <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/10/e008505.full\">Obesity-related behaviours and BMI in five urban regions across Europe: sampling design and results from the SPOTLIGHT cross-sectional survey</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Kowalczuk <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e008707.full\">Retrospective analysis of the quality of reports by author-suggested and non-author-suggested reviewers in journals operating on open or single-blind peer review models</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Azer <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/10/e008187.full\">Accuracy and readability of cardiovascular entries on Wikipedia: are they reliable learning resources for medical students?</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"2989b376-f870-5964-a103-8737524946c2"}}

